import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import accounts from "../views/accounts.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
  	path: "/accounts",
  	name: "accounts",
  	component: accounts
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
